                let viewport = window.innerHeight;
                let bodyHeight = document.body.clientHeight;
                if(bodyHeight-80 <= scroll + viewport){
                    this.$store.dispatch('moderatorCooperation/getItems', {
                        status:this.currentStatus
                    })
                }
            }*/
        },
        watch: {
            '$route': 'fetchData'
        },
    }
</script>
