<template>
    <div class="coupon">
        <router-link @click.native="activeItem(item.ID)"
                     :to="{ name: 'catalogDetail', params: { id: item.ID }}"
                     class="coupon__link"></router-link>

        <div class="coupon__wrap">
            <div class="coupon__img" :style="{ backgroundImage: 'url(//api.zemskidki.ru/'+item.PROVIDER_ICON.SRC_PREVIEW+')' }"></div>
            <div class="stat__elem" v-if="true" style="width: 122px; text-transform: uppercase; text-align: center; position: absolute;background: #000;-webkit-border-radius: 4px;-moz-border-radius: 4px;border-radius: 4px;margin: 8px;padding: 5px;color: #fff;">
                <div class="t" style="font-size: 9px;line-height: 11px;opacity: 0.5;">всего сэкономлено</div>
                <div class="n" style="font-size: 18px;line-height: 22px;">
                    {{ item.statistic.STAT_DISCOUNT_SUM_FORMAT}}
                    <span class="rouble">{{ $store.getters['options/setRouble'] }}</span>
                </div>
            </div>
            <div class="coupon__content">
                <div class="coupon__favorite">
                    <span ref="favoriteBtn" @click="setFavorite(item)" class="check-favorites" :class="{active: item.FAVORITE}"></span>
                </div>



                <div class="top">
                    <div class="coupon__company">{{ item.PROVIDER }}</div>
                </div>

                <div class="coupon__name">
                    {{ item.NAME }}
                </div>

                <div class="w">
                    <div class="coupon__economy">
                        <div class="t">
                            Экономия
                            
                            <tooltip :direction="'bottom-right-direction'"
                                     :add-class-wrap="'dib mt-2'"
                                     :add-style="{width: 376 + 'px'}">
                                <span slot="tooltipBtn" class="i i-question3 icon-btn"></span>
                                
                                <div slot="tooltipContent">
                                    <p><strong>Как рассчитывается экономия? Очень просто:</strong></p>

                                    <ul class="ul-disc-small">
                                        <li>Средний чек у данного поставщика составляет {{ item.AVERAGE }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></li>
                                        <li>Предоставляемая скидка составляет {{ item.DISCOUNTS_FROM }}%</li>
                                        <li>{{ item.DISCOUNTS_FROM }}% от {{ item.AVERAGE }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span> – это примерно {{ item.REAL_SAVINGS }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></li>
                                    </ul>

                                    <p>То есть, при обращении к данному поставщику экономия обычно составляет ≈{{ item.REAL_SAVINGS }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></p>
                                </div>
                            </tooltip>
                        </div>

                        <div class="p">
                            ≈{{ item.REAL_SAVINGS }}
                            <span class="rouble">{{ $store.getters['options/setRouble'] }}</span>
                        </div>
                    </div>


                    <div class="coupon__info">
                        Средний чек
                        {{ item.AVERAGE }}
                        <span class="rouble">{{ $store.getters['options/setRouble'] }}</span>,

                        скидка
                        <span class="n --text" v-if="item.INSTRUMENT_ACTION === 'Y'">Бесплатная <br> доставка</span>
                        <span class="n" v-else>{{ item.DISCOUNTS_FROM }}%</span>
                    </div>
                </div>


                <div class="coupon__stat">
                    <span class="elem --views">
                        <span class="i i-eye"></span>
                        <span class="t">{{ item.SHOW_COUNTER }}</span>
                    </span>

                    <span class="elem --buy" v-if="item.STAT_SOLD > 0">
                        <span class="i i-cart"></span>
                        <span class="t">{{ item.STAT_SOLD }}</span>
                    </span>
                </div>
            </div>

        </div>
        <!-- end coupon__wrap -->
    </div>
    <!-- end coupon -->
</template>

<script>
    import {flyToFavorite} from '@/helpers/animate';
    import Tooltip from '@/components/Tooltip.vue';

    export default {
        name: "RatingListItem",
        components: {
            Tooltip
        },
        props: {
            item: {
                default: function () {
                    return {}
                }
            }
        },
        methods: {
            activeItem: function (id) {
                if(id){
                    this.$store.dispatch('actions/goCoupon', {id:id});
                }
            },
            setFavorite: function (item) {
                this.$store.dispatch('actions/setFavorite', item);
                flyToFavorite(this.$refs.favoriteBtn);
            }
        }
    }
    
</script>