<script>
    import {mapState} from 'vuex';
    import Tooltip from '@/components/Tooltip.vue';
    import TextareaField from '@/components/forms/TextareaField.vue';

    export default {
        components: {
            Tooltip,
            TextareaField,
        },
        created() {},
        mounted() {
            this.fetchData();
            window.addEventListener('scroll', this.handleScroll);
        },
        data(){
            return {
                isItemListActive: 0,
            }
        },
        beforeDestroy() {
            window.removeEventListener('scroll', this.handleScroll);
        },
        computed: {
            ...mapState(
            {
                isLoad: state => state.moderatorRepayment.isLoad,
                menuList: state => state.menus.itemsAction,
                items: state => state.moderatorRepayment.items,
                scrollTop: state => state.moderatorRepayment.backData.scrollTop,
                pageCount: state => state.moderatorRepayment.backData.pageCount,
                popups: state => state.popups.items,
            }),
            defaultStatus() {
                return this.menuList['repayment'][0]['code'];
            },
            currentStatus() {
                return this.$route.params.action ? this.$route.params.action : this.defaultStatus;
            },
            tableColumnTitle() {
                let result;
                if(this.$route.params.action == 'verify'){
                    result = 'Активен';
                }
                if(this.$route.params.action == 1){
                    result = 'Отложен';
                }

                if(this.$route.params.action == 'canceled_buyer' || this.$route.params.action == 'annul' || this.$route.params.action == 'annul_provider'){
                    result = 'Аннулирован';
                }
                if(this.$route.params.action == 2){
                    result = 'Отмечено';
                }
                if(this.$route.params.action == 'work'){
                    result = 'В работе';
                }
                return result;
            }
        },
        methods: {
            fetchData () {
                this.$store.dispatch('moderatorRepayment/getItems', {
                    status: this.currentStatus,
                    reload: true
                }).then(() => {
                    setTimeout(() => {
                        if (this.scrollTop) {
                            document.body.scrollTop = document.documentElement.scrollTop = this.scrollTop;
                            this.$store.commit('moderatorRepayment/setNumPage', this.pageCount + 1);
                        }
                        this.$store.commit('moderatorRepayment/setBackData', {
                            scrollTop: 0,
                            pageCount: 0
                        });
                    }, 50);
                });

                this.$store.dispatch('moderatorRepayment/setListStatus', {
                    listStatus: this.currentStatus
                });
            },
            handleScroll: function () {
                let scroll = window.scrollY;
                let viewport = window.innerHeight;
                let bodyHeight = document.body.clientHeight;
                if(bodyHeight-80 <= scroll + viewport){
                    this.$store.dispatch('moderatorRepayment/getItems', {
                        status:this.currentStatus
                    })
                }
            },
            itemDate: function (item){
                let result;
                if(this.$route.params.action == 1 || this.$route.params.action == 2){
                    result = item.dateDiffModeratorExtended;
                }
                else if(this.$route.params.action == 'work'){
                    result = item.dateDiffWork ? item.dateDiffWork : item.dateDiffStatus;
                }
                else if(this.$route.params.action == 7){
                    result = item.statusDate;
                }
                else{
                    result = item.dateDiffStatus;
                }
                return result;
            },
            tooltipShow: function(ID) {
                let i = ID;

                if(i !== this.isItemListActive) {
                    this.isItemListActive = i;
                }

                this.$store.commit('moderatorRepayment/setItemActive', {
                    value: true,
                    index: i
                });
            },
            tooltipClose: function(ID) {
                let itemsList = this.items;
                let i = ID;

                this.$store.commit('moderatorRepayment/setItemActive', {
                        value: false,
                        index: i
                    });

                if(i == this.isItemListActive) {
                    this.$store.commit('moderatorRepayment/setItemActive', {
                        value: true,
                        index: i
                    });
                }
            },
            openHideCoupon(){
              this.$store.dispatch('popups/open', {name: this.popups.hideCoupon.name});
            },
            openMarkVerify(){
              this.$store.dispatch('popups/open', {name: this.popups.markVerify.name});
            },
        },
        watch: {
            '$route': 'fetchData'
        },
    }
</script>

